	padding-bottom:50px;
}
.orders__input{
	width:190px;
}
.level-right .orders__company:not(:last-child){
	display:inline-block;
	color:#333;
	-webkit-transition:all .4s;
	-o-transition:all .4s;
	transition:all .4s;
	margin-right:20px;
	margin-bottom:0;
}
.orders__company:hover{
	opacity:.5;
}
.orders__button{
	margin-top:8px;
}
.orders__table{
	width:100%;
	min-width:1300px;
	font-size:14px;
}
.orders__table-wrapper.loading,
.order__info.loading{
	opacity:.5;
	position:relative;
}
.orders__table-wrapper.loading:after,
.order__info.loading:after{
	display:block;
	content:'';
	background-image:url('/assets/img/svg/loading-mark.svg');
	background-size:50px 50px;
	width:50px;
	height:50px;
	position:absolute;
	top:100px;
	left:calc(50% - 25px);
