  -webkit-animation-timing-function:linear;
          animation-timing-function:linear;
}
.orders__row{
	cursor:pointer;
	-webkit-transition:all .4s ease;
	-o-transition:all .4s ease;
	transition:all .4s ease;
}
.orders__row_hide{
	display:none;
}
.orders__row:hover{
